
let getStyle = (obj,attr) => {
	return obj.currentStyle?obj.currentStyle[attr]:getComputedStyle( obj )[attr]
}
let getLocation = (onComplete=function(){},onError=function(){})=>{
	  var map = new AMap.Map('aMap', {
	      resizeEnable: true,
	      zoom: 16
	    }),
	    geolocation = null,
	    geocoder = null

	  // 
	  AMap.service('AMap.Geocoder', function() { //
	    //Geocoder
	    geocoder = new AMap.Geocoder({
	      city: "010" //
	    });
	    //TODO: geocoder 
	  });
	  // 
	  map.plugin('AMap.Geolocation', function() {
	    // 
	    geolocation = new AMap.Geolocation({
	      enableHighAccuracy: true, //:true
	      timeout: 10000, //10
	      maximumAge: 0, //00
	      convert: true, //true
	      showButton: true, //true
	      buttonPosition: 'LB', //'LB'
	      buttonOffset: new AMap.Pixel(10, 20), //Pixel(10, 20)
	      showMarker: false, //true
	      showCircle: false, //true
	      panToLocation: true, //true
	      zoomToAccuracy: true //false
	    });
	    // 
	    map.addControl(geolocation);

	    // 
	    AMap.event.addListener(geolocation, 'complete', onComplete); //
	    AMap.event.addListener(geolocation, 'error', onError); //

	    // 
	    geolocation.getCurrentPosition();
	  });
}
let checkPhone = (value)=>{
	return /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/.test(value)
}
let checkId = (value)=>{
	return /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/.test(value)
}
let checkFixedPhone = (value)=>{
	return /^((0\d{2,3})-)(\d{7,8})(-(\d{3,}))?$/.test(value)
}
let checkCredit= (value)=>{
	return /^([1-9]{1})(\d{14}|\d{18})$/.test(value)
}
export default {
	getStyle,
	getLocation,
	checkPhone,
	checkId,
	checkFixedPhone,
	checkCredit
}


