				var mySwiper = new Swiper ('.swiper-container', {
					loop:true,
					autoplay: true
				})
			},100)
			if(this.$route.query.name){
				this.title = this.$route.query.name
			}


		},
		data(){
			return {
				data:{
					id:0,
					name: '',
