				num6: 1,
				checked: false,
				checked1: false,
				title: '',
				show: false,
				radio: '1'
			}
		},
		watch: {
			'$route':function(to,from){
				if(to.name == 'hotelOrder'){
					if(this.$route.query.name){
						this.title = this.$route.query.name
					}else{
						this.title = ''
					}
				}
			}
		},
		methods:{
			handleBlur(event){
				event.path[0].blur()
			}
