<template>
	<div class="box">
		<Header title=""/>
		
			<div class="form">
				<p class="input">
					<label>:</label>
					<input type="text" placeholder="" name="">
				</p>
				<p class="input">
					<label>:</label>
					<input @blur="handleCheck" type="text" placeholder="" name="">
				</p>
				<p class="input">
					<label>:</label>
					<input type="text" placeholder="" name="">
					<span class="get"><span></span></span>
				</p>
				<p class="input">
					<label>:</label>
					<input type="text" placeholder="" name="">
				</p>
				<p class="input" style="border-bottom: none">
					<label>:</label>
					<input type="text" placeholder="******" name="">
					<span class="eye"><i class="fas fa-eye"></i></span>
				</p>
			</div>
			<div class="form">
				<p class="input">
					<label>:</label>
					<input type="text" placeholder="" name="">
				</p>
				<p class="input">
					<label>:</label>
                    <el-rate v-model="value1"></el-rate>
				</p>
				<p class="input">
					<label>:</label>
					<input @blur="hanndleFixPhone" type="text" placeholder="" name="">
				</p>
				<p class="input">
					<label>:</label>
					<input type="text" placeholder="" name="">
				</p>
				<p class="input">
					<label>:</label>
					<span class="color_aaa"> 
					<span class="down"><i class="fas fa-chevron-down"></i></span>
					</span>
				</p>
				<p class="input">
					<label>:</label>
					<span class="color_aaa"> 
					<span class="down"><i class="fas fa-chevron-down"></i></span>
					</span>
				</p>
				<p class="input">
					<label>:</label>
					<input type="text" placeholder="" name="">
				</p>
				<p class="input">
					<label>:</label>
					<span>: 13.141111 : 25.5555</span>
				</p>
				<p class="input">
					<label>:</label>
					<input @blur="handleCheckCredit" type="text" placeholder="" name="">
				</p>
				<p class="input">
					<label>:</label>
					<input type="text" placeholder="" name="">
				</p>
				<p class="input" style="border-bottom: none">
					<label>:</label>
					<input type="text" placeholder="" name="">
				</p>
			</div>
			<div class="form">
				<p class="upload"></p>
				<p class="add_icon">
				<!-- <label for="file"><i class="far fa-plus-square"></i></label>
							<input type="file" id="file" style="display: none"> -->
				   <label for="file"><i class="far fa-plus-square"></i></label>
				   <input type="file" style="display: none" id="file">
				</p>
			</div>
			<div class="form">
				<p class="hotel upload"></p>
				<p class="content"><textarea cols="46" rows="10" placeholder="..."></textarea></p>
			</div>
			<div class="body">
				<router-link class="button" tag="div" to="/enter/login">
					<span></span>
				</router-link>
				<p class="agreement"></p>
			</div>
	</div>

</template>
<script>
	import common from '../../../common/js/common'
	export default {
		data(){
			return {
				value1: null,
			}
		},
		methods: {
			handleCheck(event){
				let value = event.path[0].value
				// event.path[0].value
				// (/^1[3|4|5|8][0-9]\d{4,8}$/.test(sMobile))
				if(value){
					if(!common.checkPhone(value)){
						this.$message({
				          message: '',
				          type: 'warning'
				        });
					}
				}else{
					this.$message({
			          message: '',
			          type: 'warning'
			        });
				}
			},
			hanndleFixPhone(event){
				let value = event.path[0].value
				if(value){
					if (!common.checkFixedPhone(value)) {
						this.$message({
							message: '',
							type: 'warning'
						});
					}
				}else{
					this.$message({
						message: '',
						type: 'warning'
					})
				}
			},
			handleCheckCredit(event){
				let value = event.path[0].value
				if(value){
					if (!common.checkCredit(value)) {
						this.$message({
							message: '',
							type: 'warning'
						});
					}
				}else{
					this.$message({
						message: '',
						type: 'warning'
					})
				}
			},

		}
	}
</script>
<style scoped lang="scss">
	@import '../../../common/css/common.scss';
	.box{
		width: 100%;
		background-color: #e5e5e5;
		position: relative;
		font-size: rem(14px);
		.form{
			background-color: #ffffff;
			margin-top: rem(8px);
			padding: rem(2px) 0 0 rem(15px);
			border-top: 0.5px solid #aaa;
			border-bottom: 0.5px solid #aaa;
			position: relative;
			.input{
				position: relative;
				border-bottom: 0.5px solid #aaa;
				padding: rem(10px) 2%;
				display: flex;
				label{
					width: 32%;

				}
				input{
					width: 60%;
					border: none;
					&::-webkit-input-placeholder { 
					    color:    #aaa;
					}
					&:-moz-placeholder { 
					    color:    #aaa;
					}
					&::-moz-placeholder { 
					    color:    #aaa;
					}
					&:-ms-input-placeholder { 
					    color:    #aaa;
					}
				}
			}
			.text{
				margin-top: rem(15px);
				text-align: center;
			}
			.color_aaa{
				color: #aaa;
			}
			.down{
				position: absolute;
				top: rem(10px);
				right: 6%;
			}
			.get{
				position: absolute;
				top: 0;
				right: 0;
				background-color: #43c122;
				height: 100%;
				display: flex;
				align-items: center;
				color: #fff;
				padding: 0 rem(10px);
			}
			.eye{
				color: #43c122;
				font-size: rem(17px);
			}
			.text_img{
				width: 100%;
				height: rem(200px);
				margin-top: rem(15px);
				background-color: #eee;
				border-radius: rem(10px)
			}
			.btn{
				margin-top: rem(25px);
				button{
					width: 100%;
					border: none; 
					color: #fff;
					border-radius: rem(5px);
					background-color: #43c122;
					padding: rem(12px);
				}
			}
			.upload{
				margin: rem(10px) 0;
			}
			.add_icon{
				width: 20%;
				margin-bottom: rem(15px);
				color: #aaa;
				font-size: rem(45px);
			}
			.content{
				color: #aaa;
				textarea{
					border: none;
				}
			}
		}
		.body{
			background-color: #ffffff;
			padding: rem(16px) 5% rem(50px);
			.button{
				background-color: #43c122;
				color: #ffffff;
				text-align: center;
				padding: rem(12px) 0;
				border-radius: rem(5px);
			}
			.agreement{
				background-color: #ffffff;
				text-align: center;
				color: #769de6;
				margin-top: rem(20px);
			}
		}
	}
</style>


